.main-main {
  display: flex;
  width: 80%;
  gap: 2.5rem;
  align-items: flex-start;
}

.side-card {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.625rem;
  padding: 1.25rem;

  background-color: #7f00ff1A;
  border: 2px solid var(--main-color);
  border-radius: 1rem;

  will-change: transform;
}

.side-link {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 0.625rem;
}

.side-link a {
  display: flex;
  font-size: 2.5rem;
  transition: color 0.2s ease;
}

.icon-instagram {
  color: #e1306c;
}

.icon-linkedin {
  color: #08529B;
}

.icon-github {
  color: black;
}

.icon-mail {
  color: #AAAAAA;
}

.icon-instagram:hover,
.icon-linkedin:hover,
.icon-github:hover,
.icon-mail:hover {
  color: var(--main-color);
}

.side-text-profile {
  color: var(--text-main-color);
  font-family: 'NexonKartGothic';
  font-weight: 700;
  font-size: var(--main-font);
}

.side-text-description {
  color: var(--text-main-color);
  font-family: 'NexonKartGothic';
  font-weight: 400;
  font-size: var(--description-font);
}

.side-image-profile {
  width: var(--profile-image-size);
  height: var(--profile-image-size);
  overflow: hidden;
  border: 2px solid var(--text-main-color);
  border-radius: 100%;
}

.content-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 2rem;
  flex: 1;
}

.content-card {
  border-radius: 1rem;
  padding: 0.625rem 1.25rem;
}

.content-card h2 {
  font-family: 'NexonKartGothic';
  font-weight: 700;
  font-size: var(--title-font);
  color: var(--text-main-color);
  text-align: center;
  margin-bottom: 0.625rem;
}

.content-card.content-skill-tools {
  display: flex;
  flex-direction: column;
}

.content-block {
  padding: 0.625rem;
  border-bottom: 1px solid var(--main-color);
}

.content-block h4 {
  font-family: 'NexonKartGothic';
  font-weight: 700;
  font-size: var(--subtitle-font);
  color: var(--text-main-color);
}

.content-block p {
  font-family: 'NexonKartGothic';
  font-weight: 500;
  font-size: var(--description-font);
  color: var(--description-color);
}

.content-block .emoji {
  width: 2rem;
  display: inline-flex;
  text-align: center;
  justify-content: center;
}

.content-projects,
.content-experiences {
  grid-column: 1 / -1;
}

.grid-icon {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  gap: 1.25rem 0.625rem;
  padding: 0.625rem;
  margin-top: auto;
  margin-bottom: auto;
  width: 100%
}

.grid-skill {
  justify-content: center;
  grid-template-columns: repeat(auto-fill, 3.125rem);
}

.grid-project {
  grid-template-columns: repeat(auto-fill, 6.25rem);
}


@media (max-width: 64rem) {
  .main-main {
    flex-direction: column;
    gap: 1.5rem;
    width: 100%;
    align-items: stretch;
  }

  .side-card {
    transform: none !important;
    width: 100%;
  }

  .content-card {
    padding: 1rem;
  }

  .grid-icon {
    justify-content: center;
    gap: 1rem;
  }

  .grid-skill,
  .grid-project {
    grid-template-columns: none;
  }
}

@media (max-width: 48rem) {
  .content-grid {
    display: flex;
    flex-direction: column;
    gap: 1.5rem;
  }
}

@media (prefers-color-scheme: dark) {
  .icon-github {
    color: white;
  }
}