.blog-layout {
    display: grid;
    grid-template-columns: clamp(11.25rem, 20vw, 15rem) 1fr;
    gap: 1.875rem;
    width: 80%;
    padding: 2.5rem 1.25rem;
}

.blog-main {
    min-width: 0;
}

@media (max-width: 48rem) {
    .blog-layout {
        grid-template-columns: 1fr;
        width: 100%;
        gap: 1.5rem;
    }
}