:root {
  --main-color: #7F00FF;
  --main-color-hidden: #FFD700;
  --main-color-rgb: rgb(127, 0, 255);
  --description-color: #777777;
  --quote-background-color: #f9fafb;

  --background-main-color: #ffffff;
  --text-main-color: #000000;
  --text-active-color: #15803D;
  --text-emphasize-color: #008000;
  --text-command-color: #00b36b;
  --link-color: #2563eb;

  --main-font: 3.125rem;  
  --title-font: 2.5rem;
  --subtitle-font: 1.5625rem;
  --description-font: 1.25rem;
  --information-font: 1rem;

  --profile-image-size: 18.75rem;
  --icon-image-size: 2.5rem;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  font-size: 16px;
}

body {
  background-color: var(--background-main-color);
}

main {
  width: 80%;
  background-color: var(--background-main-color);
  margin: auto;
  padding: 2.5rem 1.25rem;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.copyright {
  justify-content: center;
  text-align: center;
  background: var(--background-main-color);
  width: 100%;
  padding-top: 2.5rem;
  padding-bottom: 7.5rem;

  font-family: 'NexonKartGothic';
  font-weight: 500;
  font-size: 1.25rem;
  color: var(--text-main-color);
}

@media (max-width: 64rem) {
  :root {
    --main-font: 2.8125rem;
    --title-font: 2.1875rem;
    --subtitle-font: 1.5625rem;
    --description-font: 1.25rem;

    --profile-image-size: 13.75rem;
  }
}

@media (max-width: 48rem) {
  :root {
    --main-font: 2.5rem;
    --title-font: 1.875rem;
    --subtitle-font: 1.25rem;
    --description-font: 1rem;

    --profile-image-size: 10rem;
  }

  main {
    width: 100%;
  }
}

@media (prefers-color-scheme: dark) {
  :root {
    --main-color: #9B6CFF;
    --main-color-rgb: rgb(155, 108, 255);
    --description-color: #BBBBBB;
    --quote-background-color: #1f2933;

    --background-main-color: #000000;
    --text-main-color: #ffffff;
    --text-active-color: #4ab973;
    --text-emphasize-color: #FFD700;
    --text-command-color: #00ff9c;
    --link-color: #93c5fd;
  }
}