.post-list {
    display: flex;
    flex-direction: column;
}

.post-item {
    display: flex;
    gap: 0.625rem;
    padding: 1.25rem;
    border-radius: 0.75rem;
    border-bottom: 1px solid var(--main-color);
    text-decoration: none;
    color: inherit;
    cursor: pointer;
    transition: background-color 0.15s ease;
}

.post-item h3 {
    font-size: var(--subtitle-font);
    color: var(--text-main-color);
}

.post-item:hover .post-title {
    text-decoration: underline;
}

.post-thumbnail {
    width: 100px;
    height: 100px;
    flex-shrink: 0;
    border-radius: 0.625rem;
    overflow: hidden;
    background-color: white;
}

.post-thumbnail img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.post-info {
    display: flex;
    flex-direction: column;
    gap: 0.375rem;
    min-width: 0;
}

.post-title {
    font-size: var(--subtitle-font);
    font-weight: 700;
    line-height: 1.3;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}

.post-meta {
    font-size: var(--information-font);
    color: var(--description-color);
}

.post-summary {
    font-size: var(--information-font);
    color: var(--description-color);
    line-height: 1.5;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
}