.skill-icon {
    position: relative;
    width: 3.125rem;
    height: 3.125rem;
}

.skill-icon img {
    width: 100%;
    height: 100%;
    display: block;
}

.tooltip {
    position: absolute;
    bottom: 120%;
    left: 50%;
    transform: translateX(-50%);

    padding: 0.25rem 0.5rem;
    font-size: 0.75rem;
    white-space: nowrap;

    background: rgba(0, 0, 0, 0.8);
    color: white;
    border-radius: 0.25rem;

    opacity: 0;
    pointer-events: none;
    transition: opacity 0.2s ease;
}

.project-icon {
    position: relative;
    display: inline-flex;
    width: 6.25rem;
    height: 6.25rem;
}

.project-icon img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    border-radius: 0.75rem;
}

.skill-icon:hover .tooltip,
.project-icon:hover .tooltip {
    opacity: 1;
}

.experience-row {
    display: grid;
    grid-template-columns: 7.5rem 1px 1fr auto;
    column-gap: 1.25rem;
    align-items: start;
}

.experience-date {
    grid-column: 1;
    grid-row: 1;
    color: var(--text-main-color);
    font-family: 'NexonKartGothic';
    font-weight: 700;
    font-size: var(--description-font);
    padding-left: 0.625rem;
}

.text-active {
    color: var(--text-active-color);
}

.experience-divider {
    grid-column: 2;
    grid-row: 1;
    align-self: stretch;
    width: 1px;
    background-color: var(--main-color);
}

.experience-content {
    grid-column: 3;
    grid-row: 1;
}

.experience-header {
    display: flex;
    align-items: flex-start;
    gap: 0.5rem;
}

.experience-name {
    color: var(--text-main-color);
    font-family: 'NexonKartGothic';
    font-weight: 700;
    font-size: var(--subtitle-font);
    flex: 1;
    min-width: 0;
}

.experience-subtitle {
    color: var(--description-color);
    font-family: 'NexonKartGothic';
    font-weight: 700;
    font-size: var(--description-font);
}

.experience-description {
    grid-column: 1 / -1;
    grid-row: 2;
    font-family: 'NexonKartGothic';
    font-weight: 500;
    font-size: var(--description-font);
    color: var(--description-color);
    margin: 0.3125rem 0.625rem;
}

.experience-url {
    font-size: var(--subtitle-font);
    color: var(--description-color);

    margin-top: 0.375rem;
    margin-left: auto;
    padding-right: 0.625rem;
    display: inline-flex;
    flex-shrink: 0;
}

.experience-url:hover {
    color: var(--main-color);
}

.experience-divider-bottom {
    grid-column: 1 / -1;
    grid-row: 3;
    height: 1px;
    background-color: var(--main-color);
    margin-bottom: 0.625rem;
}

.experience-1st-award {
    color: gold;
}

.experience-2nd-award {
    color: silver;
}

.experience-3rd-award {
    color: chocolate;
}


@media (max-width: 64rem) {
    .experience-row {
        grid-template-columns: 6.25rem 1px 1fr auto;
    }
}

@media (max-width: 48rem) {
    .project-icon {
        width: 5.5rem;
        height: 5.5rem
    }
    .experience-row {
        grid-template-columns: 5rem 1px 1fr auto;
    }
}