.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.coin {
  width: var(--profile-image-size);
  height: var(--profile-image-size); 
  perspective: 87.5rem;
  cursor: pointer;
  transition: transform 0.3s ease;
}

.coin:hover {
  transform: scale(1.08);
}

.coin-inner {
  width: 100%;
  height: 100%;
  position: relative;
  transform-style: preserve-3d;
  transition: transform 3.0s cubic-bezier(0.2, 0.8, 0.2, 1)
}

.coin-face {
  position: absolute;
  inset: 0;
  border-radius: 50%;
  overflow: hidden;
  backface-visibility: hidden;
}

.front {
  transform: rotateY(0deg);
  border: 4px solid var(--main-color);
}

.back {
  transform: rotateY(180deg);
  border: 4px solid var(--main-color-hidden);
}

.coin-face img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  border-radius: 50%;
}

.command-log {
  position: fixed;
  bottom: 1.25rem;
  left: 1.25rem;

  width: 18.75rem;
  padding: 0.75rem;

  font-family: "Fira Code", monospace;
  font-size: 0.875rem;
  color: var(--text-command-color);

  display: flex;
  flex-direction: column;
  gap: 0.25rem;

  pointer-events: none;
}

.keyboard-keys {
  position: fixed;
  top: 1.25rem;
  left: 1.25rem;
  padding: 0.625rem 0.875rem;

  display: flex;
  gap: 0.375rem;
  flex-wrap: wrap;

  font-family: "Fira Code", monospace;
  font-size: 0.8rem;
  color: var(--text-command-color);
}