@import "prismjs/themes/prism.css";

.post-header h1 {
    font-size: 2.1875rem;
    color: var(--text-main-color);
}

.post-body {
    line-height: 1.8;
    font-size: 1.1rem;
    color: var(--text-main-color);
}

.post-body a {
    color: var(--link-color);
}

.post-body blockquote {
  margin: 1.5rem 0;
  padding: 1rem 1.25rem;
  border-left: 4px solid var(--main-color);
  background: var(--quote-background-color);
  color: var(--text-main-color);
}

.post-body blockquote p {
    margin: 0;
}

.post-body figure {
  text-align: center;
}

.post-body figcaption {
  font-size: var(--information-font);
  color: var(--description-color);
}

.post-body h2 {
    margin-top: 2.1875rem;
    font-size: 1.75rem;
}

.post-body hr {
    margin-bottom: 0.75rem;
    color: var(--description-color);
}

.post-body iframe {
    width: 100%;
    height: auto;
    aspect-ratio: 16/9;
}

.post-body img {
    width: 100%;
    height: auto;
    display: block;
    margin: 0.3125rem auto;
}

.post-body ol {
    padding-left: 1.25rem;
}

.post-body p {
    margin: 1rem 0;
}

.post-body pre[class*="language-"] {
    background: var(--quote-background-color);
    padding: 1rem;
    border-radius: 0.75rem;
    overflow-x: auto;
    white-space: pre;
    max-width: 100%;
}

.post-body pre[class*="anguage-"] code {
    font-family: "Fira Code", monospace;
    font-size: 0.9rem;
    font-variant-ligatures: contextual;
    text-shadow: none;
    -webkit-font-smoothing: antialiased;
}

.post-body strong {
    color: var(--text-emphasize-color);
}

.post-body sup {
    font-size: 0.8rem;
}

.post-body table {
    width: 100%;
    table-layout: fixed;
    border-collapse: collapse;
}

.post-body table img {
    padding: 0.1rem;
}

.post-body ul {
    padding-left: 1.25rem;
}

.footnotes h2 {
  display: none;
}

.footnotes ol {
    padding-left: 1.25rem;
    margin-left: 0;
}

.footnotes li {
    margin-left: 0;
}

.footnotes p {
    margin: 0;
}

.footnotes {
  margin-top: 3rem;
  padding-top: 1.5rem;
  border-top: 1px solid var(--description-color);
}

@media (prefers-color-scheme: dark) {
    .post-body pre[class*="anguage-"] code {
        color: #e6edf3;
    }

    .token.keyword {
        color: rgb(35, 175, 235);
    }

    .token.class-name {
        color: #ff7b72;
    }
}